  background: linear-gradient(90deg, #274cd6 0%, #cf63c6 49.91%, #80d7c4 100%);
  font-family: Montserrat;
  position: relative;
  overflow: hidden;
}

.ticker-wrapper__first {
  overflow: hidden;
}

@media screen and (min-width: 320px) {
  .ticker-wrapper__first {
    width: 1000px;
  }
}

