  .ticker-wrapper__first {
    width: 100%;
  }
}

.ticker-wrapper__first, .ticker-wrapper__second {
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 9px 0;
  animation: ticker 30s linear infinite forwards;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%);
}

.ticker-wrapper__second {
  width: 100%;
  animation: ticker 30s linear 15s infinite forwards;
}

